
import { api } from "@/utils/api.js";
import { wxPay } from "@/utils/wx_sdk.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import { mapGetters, mapActions, mapMutations } from "vuex";

export default {
components: {
loading,
XHeader
},
data(){
return {
infodata: null,
goodsImgwidth: '',
winHeight: window.innerHeight - 50,
refit_id: "",
}
},
created(){
this.refit_id = this.$route.query.refit_id;
this.sub_member_id = this.$route.query.sub_member_id || 0;
this.goodsImgwidth = window.innerWidth*0.3 -20;
},
mounted(){
this.showdata();

},
methods: {
async showdata(){
let data = {
plat: 3,
account: this.account,
token: this.token,
refit_id: this.refit_id,
sub_member_id: this.sub_member_id
}
const [err, res] = await api.refitdetail(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == '2000'){
this.infodata = res.data;
}
},
copyTxt(){
this.$vux.toast.text("复制成功", "top");
},
onCopyErr(){
console.log("onCopyErr");
},
async cancelReq(data){
const [err, res] = await api.delrefit(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == '2000'){
this.$vux.toast.text('取消成功');
this.$router.push({
path: '/centerFull/myshop/subreplenishOrder'
});
}
},
calling(){
window.location.href = 'tel:4009639299'
},
receiving(refit_id,type){
let that = this;
let data = {
plat: 3,
account: this.account,
token: this.token,
refit_id: this.refit_id,
sub_member_id: this.sub_member_id
}
let content = '';
let title = '';
let fn = '';
switch(type){
case 'sure':
title = '确认收货';
content = '确认收到货物？';
fn = 'receivingReq';
break;
case 'cancel':
title = '取消订单';
content = '确定取消订单？';
fn = 'cancelReq';
break;
}
this.$vux.confirm.show({
title: title,
content: content,
onConfirm () {
that[fn](data);
}
})

},
async receivingReq(data){
const [err, res] = await api.confirmrefit(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == '2000'){
this.showdata();
}
},
async gopay(){

let that = this;
let data = {
plat: 3,
account: this.account,
token: this.token,
refit_id: this.refit_id,
pay_code: 2,
sub_member_id: this.sub_member_id?this.sub_member_id: '0',
openid: this.user.openid
}
this.$vux.loading.show({
text: '支付中...'
})
const [err, res] = await api.topay(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == '2000'){
var selfData = res;
let success = res =>{
that.showdata();
}
wxPay(this,{...res.data.pay_param,success})
}
}
},
filters: {
valFilter(val){
return val?val: '暂无'
}
},
computed: {
...mapGetters(["user", "account", "token"])
},
}



